#body .money {
  p {
    text-align: center;
    span {
      color: red
    }
  }
}

#body .vux-button-group {
  padding: 20px;
  position: relative;
  margin-top: 10px;

  p {
    position: absolute;
    top: 0;
    line-height: 1;
    color: #999999;
    font-size: 14px;
  }
}

#body .weui-msg__text-area, #body .weui-msg__opr-area {
  margin-bottom: 0;
}

#body .weui-btn-area {
  margin: 0;
}



