html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
#body .money p {
  text-align: center;
}
#body .money p span {
  color: red;
}
#body .vux-button-group {
  padding: 20px;
  position: relative;
  margin-top: 10px;
}
#body .vux-button-group p {
  position: absolute;
  top: 0;
  line-height: 1;
  color: #999999;
  font-size: 14px;
}
#body .weui-msg__text-area,
#body .weui-msg__opr-area {
  margin-bottom: 0;
}
#body .weui-btn-area {
  margin: 0;
}



